.modal {
    position: fixed;
    overflow: hidden;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: black;
    min-height: 100vh;
    z-index: 1000;

    &__wrap--poster {
        width: 100vw;
        position: relative;
        height: 450px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        object-fit: contain;

        &-Shadow {
            height: 100%;
            width: 100%;
            position: absolute;
            bottom: -2px;
            background: rgb(0, 0, 0);
            background: linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(255, 201, 2, 0) 60%);
        }

        .play {
            &__btn {
                // background: blue;
                border: none;
                outline: none;
                position: absolute;
                top: 80%;
                left: 50%;
                transform: translate(-50%, -50%);
                font-size: 5rem;
                color: rgb(41, 100, 210);
                cursor: pointer;
                // background-color: white;
            }
        }
    }



    .movie__name,
    .description {
        position: absolute;
        top: 35%;
        left: 50%;
        text-align: center;
        font-size: 2rem;
        transform: translate(-50%, -50%);
    }

    .description {
        top: 52%;
        text-align: left;
        width: 80%;
        font-size: 1.2rem;
    }



    &__btn {
        position: absolute;
        background: none;
        color: rgb(255, 255, 255);
        margin-left: 50px;
        top: 25px;
        border: none;
        padding: 10px;
        background: rgba(255, 255, 255, 0.25);
        backdrop-filter: blur(4px);
        border-radius: 5px;
        // border: 1px solid rgba(255, 255, 255, 0.18);
        font-weight: bold;
        cursor: pointer;

        &::before {
            content: "";
            position: absolute;
            // background-color: white;
            left: -15px;
            width: 0;
            height: 0;
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent;
            border-right: 7px solid white;
            // transform: rotate(45deg);
        }


    }
}