.row {



    &__posters {
        display: flex;
        overflow-x: scroll;
        overflow-y: hidden;
        padding: 20px;

        &::-webkit-scrollbar {
            display: none;
        }
    }

    &__poster {
        max-height: 100px;
        margin-right: 10px;
        transition: transform 450ms;

        &:hover {
            transform: scale(1.08);
        }

        &Large {
            max-height: 250px;
        }
    }
}